import React from 'react'
import { withNamespaces } from '../i18n'

class MyPages extends React.Component {
  static async getInitialProps() {
    return {
      namespacesRequired: ['common']
    }
  }
  render() {
    const { t } = this.props
    return (
      <React.Fragment>
        <h1>{t('h1')}</h1>
      </React.Fragment>
    )
  }
}

export default withNamespaces('common')(MyPages)